.header {
    margin: 0 auto 0 auto;
    padding-top: 45px;
    padding-bottom: 41px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 1024px) {
    .header {
        margin: 0;
        padding-top: 28px;
        padding-bottom: 31px;
    }
}